@use "base/variables" as v;
@use "base/mixins" as m;

#content {
    .btn {
        background-color: transparent;
        p {
            color: transparent;
        }
    }
}

header {
    background-image: url('../../../build/img/bg1.webp');
    width: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.header-logo {
    height: 50vw;
    display: flex;
    justify-content: center;

    @include m.phone {
        height: 25vw;
    }
}

.contact {
    margin-bottom: 5rem;

    .col {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0.5rem auto;

        .contact-1 {
            @include m.phone {
                margin-right: 1rem;
            }
        }

        .contact-2 {
            @include m.phone {
                margin-left: 1rem;
            }
        }

        &>* {
            margin: auto;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 1rem 1rem;
            border-radius: 1.75rem;
            width: 80vw;

            img {
                height: 3rem;
                display: inline-block;
            }

            p {
                padding: 0.5rem;
                margin: 0;
                color: white;
                display: inline-block;
                padding-left: 1rem;
            }

        }
    }
}

.contact-1 {
    background-color: v.$gray;

    transition: all 0.5s;
    &:hover {
        transition: all 0.5s;
        background-color: darken(v.$gray, 10%) !important;
    }
}

.contact-2 {
    background-color: v.$blue;

    transition: all 0.5s;
    &:hover {
        transition: all 0.5s;
        background-color: darken(v.$blue, 10%) !important;
    }
}

section {
    padding: 5vw 10vw;
}

.services,
.about {
    background-image: url('../../../build/img/bg2.webp');
    width: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.services {
    padding-bottom: 10rem;

    h1 {
        font-size: calc(1.5rem + 0.5vw);
        text-align: center;
        color: v.$gray;
        text-transform: uppercase;
    }

    span {
        color: v.$white;
        font-size: calc(2rem + 0.5vw);
        display: block;
        padding-top: 1rem;
        font-weight: bold;
        margin-bottom: 8rem;
    }

    .card {
        img {
            background-color: v.$white;
            margin: 2rem;
            width: auto;
            border-radius: 1.75rem;
        }

        h2 {
            color: v.$white;
            font-weight: bold;
            font-size: calc(1.75rem + 0.5vw);
        }

        P {
            color: v.$gray;
            margin: 3rem 10%;
        }

        border-radius: 1.75rem;
        background-color: v.$blue;
        border-color: v.$gray;
        transition: all 0.5s;
        &:hover {
            background-color: darken(v.$blue, 10%);
            transition: all 0.5s;
        }
    }
}

.description {
    display: flex;
    background-color: v.$background;

    @include m.tablet {
        background-image: url('../../../build/img/bg3.webp');
        width: 100%;
        background-size: cover;
        background-position: 10vw center;
        background-repeat: no-repeat;
    }

    @include m.desktop {
        padding-top: 15rem;
        padding-bottom: 15rem;
    }

    .description-text {
        background-image: none;

        @include m.tablet {
            width: 50%;
        }
    }

    h3 {
        text-align: center;
        text-transform: uppercase;
        color: v.$white;
        font-size: calc(2rem + 0.5vw);
        font-weight: bold;
        margin-bottom: 5rem;

        @include m.tablet {
            text-align: left;
        }
    }

    p {
        color: v.$white;
        font-size: calc(1.25rem + 0.5vw);
        margin-bottom: 5rem;
        text-align: center;

        @include m.tablet {
            text-align: left;
        }
    }

    .items {
        .item {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 90%;
            border: 0.1px solid v.$white;
            padding-left: 1rem;
            border-radius: 2rem;
            margin: 1.5rem auto;

            @include m.tablet {
                width: 35rem;
                margin: 1.5rem inherit;
                justify-content: start;
            }

            svg {
                color: v.$white;
                vertical-align: middle;
                width: 20;
                height: 20;
                margin-right: 1rem;
            }

            p {
                margin: 0.5rem;
                margin-bottom: 0.75rem;
            }
        }
    }

    .description-img {
        display: none;

        @include m.tablet {
            display: block;
            width: 100%;
            height: 50vw;
            z-index: 3;

            img {
                height: 100%;
            }
        }
    }
}

.about {
    h3 {
        text-align: center;
        text-transform: uppercase;
        color: v.$white;
        font-size: calc(2rem + 0.5vw);
        font-weight: bold;
        margin-bottom: 10rem;
        margin-top: 5rem;

        @include m.tablet {
            margin-top: 1.5rem;
        }
    }

    span {
        display: block;
        margin-top: 2rem;
    }
}

.contact-section {
    padding-bottom: 5rem;
    padding-top: 5rem;
    background-color: v.$background;
    display: flex;
    flex-direction: column;
    align-items: center;
    h2 {
        color: v.$white;
        margin-bottom: 2.5rem;
        text-transform: uppercase;
    }
    form {

        .form-control {
            transition: all 0.5s;
            &:focus {
                background-color: darken($color: v.$background, $amount: 10);
                border-color: v.$white;
                box-shadow: none;
                transition: all 0.5s;
                color: v.$white;
            }
        }
        .name-input {
            @include m.tablet {
                padding-right: 0 !important;
            }
        }
        input, textarea {
            color: v.$white;
            margin-bottom: 1rem;
            font-size: calc(0.75rem + 0.5vw);
            padding: 1.75rem;
            border-radius: 1rem;
            background-color: v.$background;
            &::placeholder {
                color: v.$gray;
            }
        }
        button {
            background-color: v.$gray;
            margin-top: 2rem;
            padding: 1.75rem 7.5rem;
            border: none;
            border-radius: 1rem;
            box-shadow: none;
            transition: all 0.5s;
            &:hover {
                transition: all 0.5s;
                background-color: darken(v.$gray, 10%);
            }
        }
        @include m.desktop {
            max-width: 75rem;
            margin: auto;
        }
    }
}

.thank-you {
    .text-success {
        fill: #526681;
    }
    .btn-outline-success {
        border-color: #526681;
        color: #526681;
        &:hover {
            background-color: #526681;
            border-color: #526681;
            color: white;
        }
        &:focus-visible {
            background-color: #526681;
            border-color: #526681;
            box-shadow: 0 0 0 0.25rem #526681 !important;
        }
    }
    .btn-outline-success:not(.btn-check)+.btn:active {
        background-color: #526681;
        border-color: #526681;
    }
    .btn:active {
        background-color: #526681;
        border-color: #526681;
    }

}