html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: #404B59;
  line-height: 1.5;
  height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(1.15rem + 0.3vw);
}

p {
  font-size: calc(1.5rem + 0.5vw);
}

a {
  text-decoration: none;
  color: #212121;
}

button {
  background-color: transparent;
  padding: 0.5rem 1rem;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

footer {
  margin-top: auto;
  text-align: center;
  padding: 1rem auto;
}

#content .btn {
  background-color: transparent;
}
#content .btn p {
  color: transparent;
}

header {
  background-image: url("../../../build/img/bg1.webp");
  width: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.header-logo {
  height: 50vw;
  display: flex;
  justify-content: center;
}
@media (min-width: 576px) {
  .header-logo {
    height: 25vw;
  }
}

.contact {
  margin-bottom: 5rem;
}
.contact .col {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.5rem auto;
}
@media (min-width: 576px) {
  .contact .col .contact-1 {
    margin-right: 1rem;
  }
}
@media (min-width: 576px) {
  .contact .col .contact-2 {
    margin-left: 1rem;
  }
}
.contact .col > * {
  margin: auto;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem 1rem;
  border-radius: 1.75rem;
  width: 80vw;
}
.contact .col > * img {
  height: 3rem;
  display: inline-block;
}
.contact .col > * p {
  padding: 0.5rem;
  margin: 0;
  color: white;
  display: inline-block;
  padding-left: 1rem;
}

.contact-1 {
  background-color: #a2aab3;
  transition: all 0.5s;
}
.contact-1:hover {
  transition: all 0.5s;
  background-color: #86909c !important;
}

.contact-2 {
  background-color: #526681;
  transition: all 0.5s;
}
.contact-2:hover {
  transition: all 0.5s;
  background-color: #3e4d62 !important;
}

section {
  padding: 5vw 10vw;
}

.services,
.about {
  background-image: url("../../../build/img/bg2.webp");
  width: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.services {
  padding-bottom: 10rem;
}
.services h1 {
  font-size: calc(1.5rem + 0.5vw);
  text-align: center;
  color: #a2aab3;
  text-transform: uppercase;
}
.services span {
  color: #ffffff;
  font-size: calc(2rem + 0.5vw);
  display: block;
  padding-top: 1rem;
  font-weight: bold;
  margin-bottom: 8rem;
}
.services .card {
  border-radius: 1.75rem;
  background-color: #526681;
  border-color: #a2aab3;
  transition: all 0.5s;
}
.services .card img {
  background-color: #ffffff;
  margin: 2rem;
  width: auto;
  border-radius: 1.75rem;
}
.services .card h2 {
  color: #ffffff;
  font-weight: bold;
  font-size: calc(1.75rem + 0.5vw);
}
.services .card P {
  color: #a2aab3;
  margin: 3rem 10%;
}
.services .card:hover {
  background-color: #3e4d62;
  transition: all 0.5s;
}

.description {
  display: flex;
  background-color: #404B59;
}
@media (min-width: 768px) {
  .description {
    background-image: url("../../../build/img/bg3.webp");
    width: 100%;
    background-size: cover;
    background-position: 10vw center;
    background-repeat: no-repeat;
  }
}
@media (min-width: 992px) {
  .description {
    padding-top: 15rem;
    padding-bottom: 15rem;
  }
}
.description .description-text {
  background-image: none;
}
@media (min-width: 768px) {
  .description .description-text {
    width: 50%;
  }
}
.description h3 {
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  font-size: calc(2rem + 0.5vw);
  font-weight: bold;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .description h3 {
    text-align: left;
  }
}
.description p {
  color: #ffffff;
  font-size: calc(1.25rem + 0.5vw);
  margin-bottom: 5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .description p {
    text-align: left;
  }
}
.description .items .item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 90%;
  border: 0.1px solid #ffffff;
  padding-left: 1rem;
  border-radius: 2rem;
  margin: 1.5rem auto;
}
@media (min-width: 768px) {
  .description .items .item {
    width: 35rem;
    margin: 1.5rem inherit;
    justify-content: start;
  }
}
.description .items .item svg {
  color: #ffffff;
  vertical-align: middle;
  width: 20;
  height: 20;
  margin-right: 1rem;
}
.description .items .item p {
  margin: 0.5rem;
  margin-bottom: 0.75rem;
}
.description .description-img {
  display: none;
}
@media (min-width: 768px) {
  .description .description-img {
    display: block;
    width: 100%;
    height: 50vw;
    z-index: 3;
  }
  .description .description-img img {
    height: 100%;
  }
}

.about h3 {
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  font-size: calc(2rem + 0.5vw);
  font-weight: bold;
  margin-bottom: 10rem;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .about h3 {
    margin-top: 1.5rem;
  }
}
.about span {
  display: block;
  margin-top: 2rem;
}

.contact-section {
  padding-bottom: 5rem;
  padding-top: 5rem;
  background-color: #404B59;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contact-section h2 {
  color: #ffffff;
  margin-bottom: 2.5rem;
  text-transform: uppercase;
}
.contact-section form .form-control {
  transition: all 0.5s;
}
.contact-section form .form-control:focus {
  background-color: #2b323b;
  border-color: #ffffff;
  box-shadow: none;
  transition: all 0.5s;
  color: #ffffff;
}
@media (min-width: 768px) {
  .contact-section form .name-input {
    padding-right: 0 !important;
  }
}
.contact-section form input, .contact-section form textarea {
  color: #ffffff;
  margin-bottom: 1rem;
  font-size: calc(0.75rem + 0.5vw);
  padding: 1.75rem;
  border-radius: 1rem;
  background-color: #404B59;
}
.contact-section form input::placeholder, .contact-section form textarea::placeholder {
  color: #a2aab3;
}
.contact-section form button {
  background-color: #a2aab3;
  margin-top: 2rem;
  padding: 1.75rem 7.5rem;
  border: none;
  border-radius: 1rem;
  box-shadow: none;
  transition: all 0.5s;
}
.contact-section form button:hover {
  transition: all 0.5s;
  background-color: #86909c;
}
@media (min-width: 992px) {
  .contact-section form {
    max-width: 75rem;
    margin: auto;
  }
}

.thank-you .text-success {
  fill: #526681;
}
.thank-you .btn-outline-success {
  border-color: #526681;
  color: #526681;
}
.thank-you .btn-outline-success:hover {
  background-color: #526681;
  border-color: #526681;
  color: white;
}
.thank-you .btn-outline-success:focus-visible {
  background-color: #526681;
  border-color: #526681;
  box-shadow: 0 0 0 0.25rem #526681 !important;
}
.thank-you .btn-outline-success:not(.btn-check) + .btn:active {
  background-color: #526681;
  border-color: #526681;
}
.thank-you .btn:active {
  background-color: #526681;
  border-color: #526681;
}